.staffing_item-bd .member_list { display: flex; align-items: center; }

.staffing_item-bd .member_item { position: relative; padding: 10px 15px; box-shadow: 1px 1px 4px rgba(0, 0, 0, 0); border-radius: 7px; border: 1px solid transparent; display: flex; align-items: center; }

.staffing_item-bd .member_item-pic { position: relative; width: 40px; height: 40px; border-radius: 50%; margin-right: 10px; overflow-y: hidden; }

.staffing_item-bd .member_item-pic img { position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%); }

.staffing_item-bd .member_item-delect { position: absolute; top: -11px; right: -11px; width: 22px; height: 22px; border-radius: 50%; background-color: #DB2D2D; cursor: pointer; display: none; }

.staffing_item-bd .member_item-delect:before, .staffing_item-bd .member_item-delect:after { content: ''; display: block; position: absolute; top: 50%; left: 50%; z-index: 10; width: 13px; height: 3px; background-color: #fff; }

.staffing_item-bd .member_item-delect:before { transform: translate(-50%, -50%) rotate(-45deg); }

.staffing_item-bd .member_item-delect:after { transform: translate(-50%, -50%) rotate(-135deg); }

.js-edit_mode .toggle_edit_mode { color: #2B2A35; }

.js-edit_mode .staffing_item-bd .member_item { box-shadow: 1px 1px 4px rgba(0, 0, 0, 0.2); border: 1px solid #BDBDBD; }

.js-edit_mode .staffing_item-bd .member_item-delect { display: block; }

.pgpopup_delect_success .container-inner { padding: 50px; }

.pgpopup_delect_success .pgpopup-wrap { width: 275px; }

.pgpopup_delect_success .pgpopup-bd { padding: 0; }

.pgpopup_delect_success p { margin-bottom: 30px; font-size: 28px; font-size: 1.75rem; text-align: center; }

.pgpopup_delect_success .pgbtn { display: block; width: 130px; padding: 15px; margin: auto; }

@keyframes hvr-pop { 50% { transform: scale(1.2);
    color: #334AC0; } }
